"use client";

import * as React from "react";
import { AgencyStatusCenter } from "@/components/agency-dashboard/AgencyStatusCenter";
import { ClientIntakeForm, type IntakeFormData } from "@/components/agency-dashboard/ClientIntakeForm";
import { AgencyMetricsPanel } from "@/components/agency-dashboard/AgencyMetricsPanel";
import { LiveBuildFeed, type FeedItem } from "@/components/agency-dashboard/LiveBuildFeed";
import { BuildCoPilot } from "@/components/agency/BuildCoPilot";


interface ResearchReport {
    viabilityScore: number;
    competitors: string[];
    techStack: string[];
    marketSummary: string;
}

export default function DashboardHome() {
    const [currentStage, setCurrentStage] = React.useState(1);
    const [isLoading, setIsLoading] = React.useState(false);
    const [statusMessage, setStatusMessage] = React.useState<string>();
    const [researchReport, setResearchReport] = React.useState<ResearchReport>();
    const [feedItems, setFeedItems] = React.useState<FeedItem[]>([
        { agent: "@agency-orchestrator", action: "System Initialized", time: "Just now", type: "system" },
        { agent: "@market-researcher", action: "Waiting for intake...", time: "Just now", type: "waiting" },
        { agent: "@frontend-specialist", action: "Dashboard UI loaded", time: "Just now", type: "system" },
    ]);

    const addFeedItem = (item: Omit<FeedItem, "time">) => {
        setFeedItems((prev) => [
            { ...item, time: new Date().toLocaleTimeString() },
            ...prev,
        ]);
    };

    const handleIntakeSubmit = async (data: IntakeFormData) => {
        setIsLoading(true);
        setStatusMessage("Agency Active: @market-researcher is analyzing market data...");

        addFeedItem({
            agent: "@client-intake",
            action: `New project received: "${data.projectName}"`,
            type: "success",
        });

        addFeedItem({
            agent: "@market-researcher",
            action: "Starting market viability analysis (Gemini)...",
            type: "active",
        });

        try {
            // Step 1: Create the project
            const projectRes = await fetch("/api/agency/projects", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    clientId: "demo-client",
                    idea: {
                        title: data.projectName,
                        description: data.vision,
                        targetAudience: data.audience,
                        techPreferences: data.techPreferences,
                    },
                }),
            });

            const projectData = await projectRes.json();

            if (!projectData.success) {
                throw new Error(projectData.error || "Failed to create project");
            }

            const projectId = projectData.project.id;

            addFeedItem({
                agent: "@agency-orchestrator",
                action: `Project created: ${projectId}`,
                type: "system",
            });

            // Step 2: Run research analysis
            const researchRes = await fetch("/api/agency/research", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    projectId,
                    idea: data.vision,
                    audience: data.audience,
                }),
            });

            const researchData = await researchRes.json();

            if (!researchData.success) {
                throw new Error(researchData.error || "Research analysis failed");
            }

            // Update state with results
            setResearchReport({
                viabilityScore: researchData.report.viabilityScore,
                competitors: researchData.report.competitors,
                techStack: researchData.report.techStack,
                marketSummary: researchData.report.marketSummary,
            });

            setCurrentStage(2);
            setStatusMessage(undefined);

            addFeedItem({
                agent: "@market-researcher",
                action: `Analysis complete! Viability Score: ${researchData.report.viabilityScore}/10`,
                type: "success",
            });

            addFeedItem({
                agent: "@agency-orchestrator",
                action: "Stage 1 complete. Ready for Architecture (Stage 2).",
                type: "system",
            });

        } catch (error) {
            console.error("Agency Error:", error);
            setStatusMessage(undefined);

            addFeedItem({
                agent: "@agency-orchestrator",
                action: `Error: ${error instanceof Error ? error.message : "Unknown error"}`,
                type: "error",
            });
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="space-y-8">
            <header className="flex items-center justify-between pb-2 border-b">
                <div>
                    <h1 className="text-3xl font-bold tracking-tight">Mission Control</h1>
                    <p className="text-muted-foreground mt-2">
                        Overview of your active projects and agency status.
                    </p>
                </div>
            </header>

            <section className="space-y-6">
                <AgencyStatusCenter
                    currentStage={currentStage}
                    isLoading={isLoading}
                    statusMessage={statusMessage}
                />

                <AgencyMetricsPanel
                    viabilityScore={researchReport?.viabilityScore}
                    competitors={researchReport?.competitors}
                    techStack={researchReport?.techStack}
                />

                <BuildCoPilot projectId="demo-project" currentStage={currentStage} />

                <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">

                    <div className="lg:col-span-1">
                        <ClientIntakeForm onSubmit={handleIntakeSubmit} isLoading={isLoading} />
                    </div>
                    <div className="lg:col-span-2">
                        <LiveBuildFeed items={feedItems} />
                    </div>
                </div>
            </section>
        </div>
    );
}
